@import url(https://fonts.googleapis.com/css2?family=Arapey&family=Montserrat&display=swap);
/*===== GOOGLE FONTS =====*/
/*===== BASE =====*/
*,::before,::after {
  box-sizing: border-box;
  margin: 0;
}

body {
  padding: 0;
  font-family: 'Arapey', serif;
}

h1,p,ul {
  margin: 0;
}

ul {
  padding: 0;
  list-style: none;
}

a, a:visited {
  text-decoration: none;
  color: inherit;
}

img {
  max-width: 100%;
  height: auto;
}


.searchLink__container {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100vw;
    height: 100vh;
    background-color: rgba(0,0,0,0.4);
    position: fixed;
    z-index: 100;
}

.searchLink {
    z-index: 100;
    position: fixed;
    background-color: #fff;
    display: flex;
    /* justify-content:center; */
    align-items: center;
    flex-direction: column;
    width: 80vw;
    max-width: 1000px;
    box-shadow: 1px 1px 3px 5px rgba(0,0,0,0.2);
    border-radius: 2%;
    padding: 1em 2em;
    padding-bottom: 4em;
}

.searchLink p {
    margin: 0 2em;
}

.searchLink__close {
    align-self: flex-end;
    margin-bottom: 2em;
    cursor: pointer;
}


.announcement {
    text-align: center;
    background: #ffdbe9;
    padding: 13.5px 30px;
}

.MuiBadge-colorPrimary {
    color: #fff;
    background-color: #ffdbe9 !important;
}

.navbar__container {
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    height: 90px;
    padding: 15px 0;
    align-items: center;
}

.logo {
    font-size: 20px;
    font-weight: 900;
    font-family: 'Montserrat', sans-serif;
    letter-spacing: 0.1em;
}

.nav__item {
    font-family: 'Montserrat', sans-serif;
    font-weight: 900;
}

.nav__show {
    display:none;
}

.icon__container {
    display: flex;
    flex-direction: row;
    justify-content: center;
}

.nav__person, .nav__search, .nav__cart {
    margin: 0 1em;
    align-self: center;
}

.nav__person, .nav__search {
    display: none;
}

.nav__menu{
    position: fixed;
    top: 0;
    left: -100%;
    width: 50%;
    height: 100%;
    padding: 3rem 1.5rem 0;
    background: #1f2021;
    -webkit-backdrop-filter: blur(10px);
            backdrop-filter: blur(10px);
    transition: 0.5s;
    overflow: auto;
    color: #ffff;
    display: flex;
    flex-direction: column;
    z-index: 1;
}

.nav__menu li {
    font-family: 'Arapey', serif;
    font-style: normal;
}

.show {
    left: 0;
}

.nav__home__menu, .nav__product__menu, .nav__news__menu {
    font-size: 22px;
    margin-bottom: 1em;
}

.nav__login, .nav__create {
    margin-bottom: 0.5em;
    color: #BFBFBF;
}

.menu {
    width: 80px;
    text-align: center;
    padding: 10px 0;
    cursor: pointer;
}

@media screen and (min-width: 1024px) {
    .menu {
        display:none;
    }

    .nav__show {
        display: block;
    }

    .nav__list { 
        display: flex;
        flex-direction: row;
        justify-content: space-evenly;
        width: 570px;
        align-items: center;
        font-size: 14px;
    }

    .logo {
        font-size: 25px;
    } 

    .nav__person, .nav__search {
        display: block;
    }    

    .nav__menu{
        width: 40%;
    }
}
.banner__img {
    width:100%;
    height: 100vh;
    object-fit: cover;
    object-position: 50% 15%;
}

.shop__btn {
    font-family: 'Montserrat', sans-serif;
    font-weight: 600;
    font-style: normal;
    letter-spacing: 0.1em;
    text-transform: uppercase;
    padding: 12px 20px;
    font-size: 0.8125em;
    background-color: #000;
    color: #fff;
    margin-top: 1em;
    width: 131.36px;
    margin: auto;
}

.productHome__img__container {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
}

.home__container { 
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
}

.hero__banner {
    text-align: center;
    margin-top: 1em;
}

.hero__subtitle { 
    font-size: 1.125em;
    font-family: Montserrat,sans-serif;
    font-weight: 600;
    font-style: normal;
    letter-spacing: .1em;
    text-transform: uppercase;
    margin-bottom: .94rem;
}

.hero__title {
    line-height: 1.25;
    letter-spacing: 0.05em;
    font-size: 3.125em;
    display: block;
    font-family: "Old Standard TT",serif;
    font-weight: 400;
    font-style: normal;
    line-height: 1.4;
    text-rendering: optimizeLegibility;
}

.home__description {
    width: 60vw;
    margin: 3em 0;
    font-size: 19px;
}

.featured__title {
    font-size: 30px;
    font-weight: 400;
}

@media screen and (min-width: 768px) {
    .productHome__img__container {
        max-width: 565px;
        margin-top: 2em;
    }

    .banner__slide { 
        position: relative;
        text-align: center;
    }

    .hero__banner {
        position: absolute;
        top: 45%;
        left: 50%;
        -webkit-transform: translate(-50%, -50%);
                transform: translate(-50%, -50%);
    }
    
    .hero__subtitle { 
        font-size: 1.125em;
        font-family: Montserrat,sans-serif;
        font-weight: 600;
        font-style: normal;
        letter-spacing: .1em;
        text-transform: uppercase;
        margin-bottom: .94rem;
    }
}

