.security {
    width: min(80%, 300px);
    color: #767676;
    font-size: 12px;
    text-align: center;
}

.security a:visited {
    color: inherit;
}

.security a {
    text-decoration: underline;
}

.toggle__sign {
    margin-bottom: 2em;
    margin-top: 0.5em;
    align-self: flex-start;
}

.toggle__sign__p {
    text-decoration: underline;
    color: #0000EE;
    cursor: pointer;
}

.hr--large {
    margin: 60px auto;
    border-top: solid #000;
    border-width: 1px 0 0;
    width: 50px;
}

.hr--small, .hr--small-top {
    margin: 15px auto;
    border-top: solid #000;
    border-width: 1px 0 0;
    width: 50px;
}

@media screen and (min-width: 768px) {
    .button__logo {
        margin-right: 4rem;
        margin-left: 1rem;
    }
    
    .page__container {
        margin-top: 2em;
    }

    .auth__container {
        padding-top: 80px;
        margin-top: 2em;
    }
    
    .login__container {
        flex-direction: row;
        width: 774px;
        flex-wrap: wrap;
    }

    .divider {
        flex-direction: column;
        align-items: center;
        width: 121.8px;
        height: 150px;
    }

    .form__container {
        width: 315px;
        height: 187px;
        display: flex;
        justify-content: center;
    }

    .social__auth__container {
        width: 326.59px;
        height: 187px;
        justify-content: stretch;
    }

    .social__button {
        height: 55px;
    }

    .divider hr {
        height: 68px;
        border: none;
        width: 1px;
    }

    .toggle__sign {
        margin-top: 2em;
        
    }

    .security {
        margin: 0;
    }
}

@media screen and (max-width: 400px) {
    .form__container, .divider, .login__container, .social__auth__container { 
        width: 330px;
    }
}
.slider {
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
}
.radio-toolbar {
    margin: 10px;
    text-align: center;
}
  
.radio-toolbar input[type="radio"] {
    opacity: 0;
    position: fixed;
    width: 0;
}
  
.radio-toolbar label {
    display: inline-block;
    padding: 9px 11px;
    font-family: 'Montserrat', sans-serif;
    font-size: 13px;
    margin: 3px 4px 7px 3px;
    cursor: pointer;
    font-weight: 700;
}
  
.radio-toolbar input[type="radio"]:focus + label {
    border: 1px solid #000;
}
  
.radio-toolbar input[type="radio"]:checked + label {
    border: 1px solid #000;
}

